
*{
  box-sizing: border-box;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.style0{
  display: flex;
  align-items: center;
  justify-content: center;
  background: #1f2026;
  position: relative;
  margin: 20px;
  border-radius: 35px;
  width: 100%;
}
.style0::before{
  content: '';
  position: absolute;
  width: 100%;
  background: #ebbbbb;
  clip-path: inset(47% 0 0 0);
  z-index: -1;
  height: 100%;
}
.style0::selection{
  background:	#FF676D;
  color: #fff;
}
 .container0{
  max-width: 950px;
  width: 100%;
  overflow: hidden;
  padding: 20px;
  margin-top: 40px;
  margin: 10px;
 
}
.card0{
  background-color: #fff;
  border-radius: 25px;
  padding: 10px;
 
  
}
.content0{
  padding: 20px;
  
}
.container0 .main-card{
  display: flex;
  justify-content: space-evenly;
  width: 200%;
  transition: 1s;
}
#two:checked ~ .main-card{
  margin-left: -100%;
}
.container0 .main-card .cards0{
  width: calc(100% / 2 - 10px);
  display: flex;
  flex-wrap: wrap;
  margin: 0 20px;
  justify-content: space-between;
}
.main-card0 .cards0 .card0{
  width: calc(100% / 3 - 10px);
  border-radius: 12px;
  padding: 30px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.25);
  transition: all 0.4s ease;
}
.main-card0 .cards0 .card0:hover{
  transform: translateY(-15px);
}
.cards0 .card0 .content0{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.cards0 .card0 .content0 .img0{
  height: 130px;
  width: 130px;
  border-radius: 50%;
  padding: 3px;
  background: #FF676D;
  margin-bottom: 14px;
}
.card0 .content0 .img0 img{
  height: 100%;
  width: 100%;
  border: 3px solid #ffff;
  border-radius: 50%;
  object-fit: cover;
}
.card .content .name{
  font-size: 20px;
  font-weight: 500;
}
.card0 .content0 .job{
  font-size: 20px;
  color: #FF676D;
}
.card0 .content0 .media-icons{
  margin-top: 10px;
  display: flex;

}
.media-icons a{
  text-align: center;
  line-height: 33px;
  height: 35px;
  width: 35px;
  margin: 0 4px;
  font-size: 14px;
  color: #FFF;
  border-radius: 50%;
  border: 2px solid transparent;
  background: #FF676D;
  transition: all 0.3s ease;
}
.media-icons a:hover{
  color: #FF676D;
 
  border-color: #FF676D;
}
 .container0 .button0{
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 20px;
}
.button0 label{
  height: 15px;
  width: 15px;
  border-radius: 20px;
  
  margin: 0 4px;
  cursor: pointer;
  transition: all 0.5s ease;
}
.button0 label.active{
  width: 35px;
}
#one:checked ~ .button0 .one{
  width: 35px;
}
#one:checked ~ .button0 .two{
  width: 15px;
}
#two:checked ~ .button0 .one{
  width: 15px;
}
#two:checked ~ .button0 .two{
  width: 35px;
}
input[type="radio"]{
  display: none;
}
@media (max-width: 768px) {
  .main-card .cards0 .card0{
    margin: 20px 0 10px 0;
    width: calc(100% / 2 - 10px);
  }
}
@media (max-width: 600px) {
  .main-card .cards0 .card0{
    margin: 20px 0 10px 0;
    width: 100%;
  }
}



.style1{
    background-color: rgb(26, 176, 206);
    border-radius: 1rem;
    margin: 2em;
    box-shadow: -2px 0px 9px rgb(13, 62, 72);
}
.container1 {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  /* max-width: 1400px; */
  /* margin-block: 2rem; */
  gap: 2rem;
  padding:1em;

}

.style1 img {
  max-width: 100%;
  display: block;
  object-fit: cover;
}

.card {
  display: flex;
  flex-direction: column;
  width: 280px;
  /* width: clamp(20rem, calc(20rem + 2vw), 22rem); */
  overflow: hidden;
  box-shadow: 0 .1rem 1rem rgba(0, 0, 0, 0.1);
  border-radius: 1em;
  background: #ECE9E6;
  background: linear-gradient(to right, #FFFFFF, #ECE9E6);

}
.card__body {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  margin: auto;
  /* gap: .5rem; */
}
.card__body h4{
    font-size:80px;
}
.card__body p{
    font-size: 15px;
}
.tag {
  align-self: flex-start;
  padding: .25em .75em;
  border-radius: 1em;
  font-size: .75rem;
}

.tag + .tag {
  margin-left: .5em;
}

.tag-blue {
  background: #56CCF2;
  background: linear-gradient(to bottom, #2F80ED, #56CCF2);
  color: #fafafa;
}

.tag-brown {
  background: #D1913C;
background: linear-gradient(to bottom, #FFD194, #D1913C);
  color: #fafafa;
}

.tag-red {
  background: #cb2d3e;
background: linear-gradient(to bottom, #ef473a, #cb2d3e);
  color: #fafafa;
}

.card__body h4 {
  font-size: 1.5rem;
  text-transform: capitalize;
}

.card__footer {
  display: flex;
  padding: 1rem;
  margin-top: auto;
}

.user {
  display: flex;
  gap: .5rem;
}

.user__image {
  border-radius: 50%;
}

.user__info > small {
  color: #666;
}

