.cards-wrapper {
  display:flex;
  flex-wrap:wrap;
  justify-content: center;
  align-items: center;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 2rem;
  padding: 1.5rem;
  margin: 0 auto;
  /* max-width: 100%; */
}
.card3 {
  font-family: 'Heebo';
  --bg-filter-opacity: 0.7;
  background-image: linear-gradient(rgba(0,0,0,var(--bg-filter-opacity)),rgba(0,0,0,var(--bg-filter-opacity))), var(--bg-img);
  height: 13em;
  width: 8em;
  font-size: 1.5em;
  color:#fff;
  border-radius: 1em;
  padding: 1em;
  display: flex;
  align-items: flex-end;
  background-size: cover;
  background-position: center;
  box-shadow: 0 0 5em -1em black;
  transition: all, var(--transition-time);
  position: relative;
  overflow: hidden;
  border: 2px solid #ccc;
  text-decoration: none;
}
  .card3 h1 {
  margin: 0;
  margin-top: 90px;
  font-size: 0.6em;
  line-height: 1.5em;
  color: #FFFFFF;  
}
  .card3 p {
  font-size: 0.65em;
  font-family: 'Open Sans';
  margin-top: .4em;
  line-height: 1.2em;
  opacity: .7;
  margin-bottom: 50px;
  color: #FFFFFF;
}

  .card3 .tags {
  display: flex;
}

 .card3 .tags .tag {
  font-size: 0.65em;
  background: rgba(255,255,255,0.5);
  border-radius: 0.3rem;
  padding: 0 0.5em;
  margin-right: 0.5em;
  line-height: 1.5em;
  transition: all, var(--transition-time);
}

 .card3:hover {
  background: var(--color);
  color: rgb(31, 23, 23);
  background-color: #ababdb;
  border: 2px solid rgb(152, 68, 68);
  box-shadow: -30px 30px 30px -30px rgba(217, 25, 25, 0.5);
}

  .card3 .date {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 0.45em;
  padding: 1em;
  line-height: 1em;
  opacity: .8;
}
.card3:before {
  background: #ddd;
  width: 250%;
  height: 250%;
}

.card3:after {
  background: white;
  width: 200%;
  height: 200%;
}

.card3:hover {
  color: var(--color);
}

.card3:hover:before, .card:hover:after {
  transform: scale(1);
}


